@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    padding: 0px;
    margin: 0px;
}

body {
    direction: rtl;
}


header {
    background-color: #3d4257;
    height: 183px;
}

.topTitle {
    height: 55px;
}

.topTitle>h3 {
    text-align: center;
    font-size: 18px;
    color: #B7BCCC;
    padding-top: 16px;
    font-family: 'Assistant', sans-serif;
}

.topTitle>span {
    position: absolute;
    top: 16px;
    right: 16px;
    font-size: 18px;
    color: #B7BCCC;
}

.image {
    text-align: center;
}

.image>h2 {
    font-size: 20px;
    color: white;
    font-family: 'Assistant', sans-serif;
}

.image>img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    align-items: center;
    margin-top: 27px;
}

.navbar {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-around;
    box-shadow: 2px 2px 2px 2px #ccc;
}

.navbar>.buttons {
    padding: 11px;
    text-align: center;
    font-size: 14px;
    font-family: 'Assistant', sans-serif;
}

.borderBottom {
    border-bottom: 3px solid #324483;
}

.btn {
    padding: 14px;
    text-align: center;
    width: 90%;
    background-color: #3D4257;
    margin: 20px auto;
    border-radius: 8px;

}

.btn>a {
    color: white;
    font-size: 16px;
    font-family: 'Assistant', sans-serif;

}


.contents {
  width: 100%;
  height: 50%;
}


.block {
    padding: 8px;
    width: 90%;
    margin: 0 auto;
    border-bottom: 2px solid #E2E2EA;
}

.block>.title {
    color: #324483;
    font-size: 16px;
    font-family: 'Assistant', sans-serif;
}

.block>.text {
    color: #717480;
    font-size: 12px;
    opacity: 60%;
    font-family: 'Assistant', sans-serif;
}

.block>.link {
    color: #324483;
    font-size: 12px;
    font-family: 'Assistant', sans-serif;
}
